// profile/actions.ts
import { ActionTree, mapState } from 'vuex';
import axios from 'axios';
import { FloodState, FloodStation} from './types';
import { RootState } from '../types';

export const actions: ActionTree<FloodState, RootState> = {
    fetchFloodStation({ commit }): any {
        axios.get("/station/flood").then((response) => {
            const payload: Array<FloodStation> = response && response.data;
            commit('loadStation', payload);
        }).catch(err => alert(err))
    }
};