<template>
  <div id="OMap" style="width: 100%;height: 100%;"></div>
</template>
<style scoped>
</style>

<script>
import proj4 from "proj4";

//------------------------------,,------------------------------
proj4.defs([
  [
    "EPSG:4326",
    "+title=WGS 84 (long/lat) +proj=longlat +ellps=WGS84 +datum=WGS84 +units=degrees"
  ],
  [
    "EPSG:3826",
    "+title=TWD97 TM2+proj=tmerc +lat_0=0 +lon_0=121 +k=0.9999 +x_0=250000 +y_0=0 +ellps=GRS80 +units= +no_defs"
  ],
  [
    "EPSG:3828",
    "+proj=tmerc +lat_0=0 +lon_0=121 +k=0.9999 +x_0=250000 +y_0=0 +ellps=aust_SA"
  ]
]);
const EPSG3826 = new proj4.Proj("EPSG:3826");
//------------------------------,---------------------------------------

import { mapActions, mapState } from "vuex";
export default {
  name: "tgosMap",
  mounted() {
    this.initTGOS();
  },
  data() {
    return {
      pOMap: undefined,
      pMap: undefined,
      activeMarker: []
    };
  },
  computed: {
    ...mapState(["activeMap"]),
    ...mapState("rain", {
      rainStations: state => state.stations
    }),
    ...mapState("water", {
      waterStations: state => state.stations
    }),
    ...mapState("flood", {
      floodStations: state => state.stations
    }),
    ...mapState("realtime", {
      realtimeStations: state => state.stations
    })
  },
  watch: {
    activeMap(newValue, oldValue) {
      if (newValue === 0) {
        // 
      } else if (newValue === 1) {
        this.fetchRainStation();
      } else if (newValue === 2) {
        this.fetchWaterStation();
      } else if (newValue === 3) {
        this.fetchFloodStation();
      } else if (newValue === 4) {
        this.fetchRealtimeStation();
      } else if (newValue === 5) {
        // 
      }
    },
    rainStations(newValue, oldValue) {
      if (this.activeMap === 1) this.prepareRainStationMarker();
    },
    waterStations(newValue, oldValue) {
      if (this.activeMap === 2) this.prepareWaterStationMarker();
    },
    floodStations(newValue, oldValue) {
      if (this.activeMap === 3) this.prepareFloodStationMarker();
    },
    realtimeStations(newValue, oldValue) {
      if (this.activeMap === 4) this.prepareRealtimeStationMarker();
    }
  },
  methods: {
    ...mapActions("rain", ["fetchRainStation"]),
    ...mapActions("water", ["fetchWaterStation"]),
    ...mapActions("flood", ["fetchFloodStation"]),
    ...mapActions("realtime", ["fetchRealtimeStation"]),
    initTGOS() {
      //--------------------------------------
      this.pOMap = document.getElementById("OMap");
      let mapOptiions = {
        /*
        mapTypeControl: true, 
        mapTypeControlOptions: {
          mapTypeIds: [
            TGOS.TGMapTypeId.TGOSMAP,
            TGOS.TGMapTypeId.NLSCMAP,
            TGOS.TGMapTypeId.ROADMAP
          ],
          controlPosition: TGOS.TGControlPosition.RIGHT_TOP,
          mapTypeControlStyle: TGOS.TGMapTypeControlStyle.DEFAULT
          //mapTypeControlStyle(, /  / )
        }, */
        navigationControl: false,
        navigationControlOptions: {
          controlPosition: TGOS.TGControlPosition.LEFT_BOTTOM, //
          navigationControlStyle: TGOS.TGNavigationControlStyle.SMALL //
        },
        scaleControl: true
      };

      this.pMap = new TGOS.TGOnlineMap(
        this.pOMap,
        TGOS.TGCoordSys.EPSG3826,
        mapOptiions
      ); //,TWD97

      let twd97 = proj4(EPSG3826, [120.52636, 23.699594]);
      this.pMap.setZoom(7); //
      this.pMap.setCenter(new TGOS.TGPoint(twd97[0], twd97[1])); //
      //---------------------------------------
    },
    clearActiveMarker() {
      for (let marker of this.activeMarker) {
        marker.setMap(null);
      }
      this.activeMarker.splice(0, this.activeMarker.length);
    },
    prepareStationMarker(imgUrl, stations, msgFun) {
      this.clearActiveMarker();

      let markerImg = new TGOS.TGImage(
        imgUrl,
        new TGOS.TGSize(38, 33),
        new TGOS.TGPoint(0, 0),
        new TGOS.TGPoint(10, 33)
      ); //

      let InfoWindowOptions = {
        maxWidth: 4000, //
        pixelOffset: new TGOS.TGSize(5, -30), //InfoWindow, TGSize, X, Y
        zIndex: 1000 //
      };
      let pMap = this.pMap;
      for (let station of stations) {
        let log = parseFloat(station.Location.Longitude);
        if (isNaN(log)) continue;

        let lat = parseFloat(station.Location.Latitude);
        if (isNaN(lat)) continue;

        let twd97 = proj4(EPSG3826, [log, lat]);
        let point = new TGOS.TGPoint(twd97[0], twd97[1]);
        let pTGMarker = new TGOS.TGMarker(pMap, point, station.Name, markerImg); //

        //10min1hr3hr6hr12hr24hr
        let context = msgFun(station);
        let messageBox = new TGOS.TGInfoWindow(
          context,
          point,
          InfoWindowOptions
        ); //

        let openInfoWindow = () => messageBox.open(pMap);
        let closeInfoWindow = () => messageBox.close();

        TGOS.TGEvent.addListener(pTGMarker, "mouseover", openInfoWindow); //--
        TGOS.TGEvent.addListener(pTGMarker, "mouseout", closeInfoWindow); //--
        this.activeMarker.push(pTGMarker);
      }
    },
    prepareRainStationMarker() {
      const imgUrl =
        "http://icons.iconarchive.com/icons/google/noto-emoji-travel-places/24/42686-umbrella-with-rain-drops-icon.png"; //

      const stations = this.rainStations;
      let msgFun = station => `
        <div class="text-left">
          <strong>:</strong>${station.Name}<br/>
          <strong>:</strong>${station.Code}<br/>
          <strong>:</strong>${station.District}<br/>
          <strong>10:</strong>${station.AccRain.Acc10min}<br/>
          <strong>1:</strong>${station.AccRain.Acc1h}<br/>
          <strong>3:</strong>${station.AccRain.Acc3h}<br/>
          <strong>6:</strong>${station.AccRain.Acc6h}<br/> 
          <strong>12:</strong>${station.AccRain.Acc12h}<br/> 
          <strong>24:</strong>${station.AccRain.Acc24h}<br/> 
          <strong>:</strong>${station.Owner}<br/> 
          <strong>:</strong>${station.Timestamp}<br/> 
        </div>
        `;
      this.prepareStationMarker(imgUrl, stations, msgFun);
    },
    prepareWaterStationMarker() {
      const imgUrl =
        "http://icons.iconarchive.com/icons/google/noto-emoji-travel-places/24/42699-water-wave-icon.png"; //

      const stations = this.waterStations;
      /*
         Drainage: string // 

      */
      let msgFun = station => `
        <div class="text-left">
          <strong>:</strong>${station.Name}<br/>
          <strong>:</strong>${station.Code}<br/>
          <strong>:</strong>${station.District}<br/>
          <strong>:</strong>${station.Elevation}<br/>
          <strong>:</strong>${station.ObservationPrinciple}<br/>
          <strong>:</strong>${station.ObservationFrequency}<br/>
          <strong>:</strong>${station.WaterLevel}<br/> 
          <strong>:</strong>${station.AlarmThreshold1}<br/> 
          <strong>:</strong>${station.AlarmThreshold2}<br/> 
          <strong>:</strong>${station.LevelHeight}<br/> 
          <strong>:</strong>${station.AlarmState}<br/> 
          <strong>:</strong>${station.Drainage}<br/> 
          <strong>:</strong>${station.Owner}<br/> 
          <strong>:</strong>${station.Timestamp}<br/> 
        </div>
        `;
      this.prepareStationMarker(imgUrl, stations, msgFun);
    },
    prepareFloodStationMarker() {
      const imgUrl =
        "http://icons.iconarchive.com/icons/simplefly/simple-green/24/water-sea-icon.png"; //

      const stations = this.floodStations;
      let msgFun = station => `
        <div class="text-left">
          <strong>:</strong>${station.Name}<br/>
          <strong>:</strong>${station.Code}<br/>
          <strong>:</strong>${station.District}<br/>
          <strong>:</strong>${station.FloodLevel}<br/>
          <strong>:</strong>${station.Owner}<br/> 
          <strong>:</strong>${station.Timestamp}<br/> 
        </div>
        `;
      this.prepareStationMarker(imgUrl, stations, msgFun);
    },
    prepareRealtimeStationMarker() {
      const imgUrl =
        "http://icons.iconarchive.com/icons/iconshock/real-vista-security/24/monitor-icon.png"; //

      const stations = this.realtimeStations;
      let msgFun = station => `
        <div class="text-left">
          <strong>:</strong>${station.Name}<br/>
          <strong>:</strong>${station.District}<br/>
          <strong>:</strong>${station.Owner}<br/>
        </div>
        `;
      this.prepareStationMarker(imgUrl, stations, msgFun);
    }
  }
};
</script>