// profile/actions.ts
import { ActionTree, mapState } from 'vuex';
import axios from 'axios';
import { RealtimeState, RealtimeStation } from './types';
import { RootState } from '../types';

export const actions: ActionTree<RealtimeState, RootState> = {
    fetchRealtimeStation({ commit }): any {
        axios.get("/station/realtime").then((response) => {
            const payload: Array<RealtimeStation> = response && response.data;
            console.log(payload)
            commit('loadStation', payload);
        }).catch(err => alert(err))
    }
};