// profile/mutations.ts
import { MutationTree } from 'vuex';
import { WaterStation, WaterState } from './types';

export const mutations: MutationTree<WaterState> = {
    loadStation(state, payload: Array<WaterStation>) {
        state.stations.splice(0, state.stations.length)
        for (let station of payload) {
            state.stations.push(station)
        }
    }
};