// profile/actions.ts
import { ActionTree, mapState } from 'vuex';
import axios from 'axios';
import { WaterState, WaterStation } from './types';
import { RootState } from '../types';

export const actions: ActionTree<WaterState, RootState> = {
    fetchWaterStation({ commit }): any {
        axios.get("/station/WaterLevel").then((response) => {
            const payload: Array<WaterStation> = response && response.data;
            commit('loadStation', payload);
        }).catch(err => alert(err))
    }
};